@import './reset.css';

:root {
    --color-1: #023535;
    --color-2: #015958;
    --color-3: #008f8c;
    --color-4: #0caba8;
    --color-5: #0fc2c0;

    --border-radius: 8px;

    --animation-duration: 300ms;

    --cursor-pointer: pointer;

    --font-KCC: 'KCC-Ganpan';
}

@font-face {
    font-family: 'KCC-Ganpan';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302@1.0/KCC-Ganpan.woff2')
        format('woff2');
    font-weight: normal;
    font-style: normal;
}

body {
    width: 100%;
    height: 100%;
    background-color: var(--color-1);
    padding: 70px;
}

.main_wrap {
    font-family: 'KCC-Ganpan';
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

#navbar h2 {
    font-size: 72px;
    color: var(--color-5);
    margin-bottom: 72px;
}

#cards {
    color: var(--color-3);
    border-bottom: 2px solid var(--color-5);
    margin: 36px 36px 36px 0;
    font-size: 18px;
}

#cards h4 {
    font-size: 36px;
    margin-bottom: 18px;
}

#cards p {
    margin-bottom: 18px;
}

#cards .count {
    margin: 0 9px;
    font-weight: bold;
}

#cards button {
    margin-bottom: 36px;
    color: var(--color-1);
    background-color: var(--color-5);
    border: none;
    outline: none;
    border-radius: var(--border-radius);
    font-size: 18px;
    padding: 4.5px 9px;
    line-height: 1.5;
    cursor: var(--cursor-pointer);
    font-weight: bold;
}

#input_form input,
#input_form button {
    border: none;
    padding: 9px;
    font-size: 18px;
    border-radius: 4px;
}

#input_form input {
    margin-right: 9px;
    color: var(--color-1);
}

#input_form button {
    color: var(--color-1);
    background-color: var(--color-5);
    font-weight: bold;
}
